.detail_item{
          display: flex;
          width: 100%;
          flex-direction: column;
          align-items: center;
          padding: 40px;
}
.detail_item_container{
          display: flex;
          width: 800px;
          justify-content: space-between;
}

.item_pic_container{
          padding: 40px;
}
.item_pic_container img{
          width: 100%;
          height: 100%;
}
.item_pic{
          border-right: 0.5px solid black;
          width: 60%;
}
.item_info_container{
          padding: 40px;
}
.row{
          width: 100px;
          padding: 5px;
          display: flex;
}
.row label{
          text-align: left;
          width: 60px;
}
.item_id{
          color: rgb(189, 186, 186);
}
.buybtn{
          display: flex;
          padding: 40px;        
}
