.product{
          width: 100%;
}
.product_container{
          display: flex;
          width: 100%;
          
}
.list_main_container{
          display: flex;
          flex-wrap: wrap;
          justify-content: center;
}
.item_container{
          padding: 10px;

          height: auto;
}
.product_item{
          width: 23%;
          padding: 5px;
          font-size: 11pt;
          text-align: center;
          margin-bottom: 40px;
          margin-right: 50px;
          border-radius: 8px;
}
.list_prod{
          width: 90%;
          padding-left: 20px;
}
.list_top{
          display: flex;
          justify-content: space-between;
          width: 100%;
}
.filter{
          width: 20%;
          padding-right: 10px;
          
}
.filter_container .filter_by{
          padding-top: 10px;
          padding-bottom: 10px;
          border-bottom: 0.5px solid black;
}
.filter_by:last-child{
          border: none;
}
.item{
          display: flex;
          flex-direction: column;
          align-items: center;
          
}
.link_pic{
          width: 100%;
          height: 150px;
          padding-bottom: 6px;
}
.link_pic img{
          width: 100%;
          max-height: 90%;
          
}
.link_content{
          padding-top: 10px;
          font-size: 12pt;
}
.item h4{
          color: rgba(87, 145, 28, 0.591);
}
@media only screen and (max-width: 1200px) {
          
          .product_container{
                    flex-direction: column;
                    width: 100%;   
          }
          .product_item{
                    width: 100px;
                    padding: 0;
                    font-size: 11pt;
                    text-align: center;
                    margin-bottom: 20px;
                    margin-right: 20px;
                    border-radius: 8px;
          }
          .item_container{
                    padding: 0;
                    height: auto;
          }
          .item{
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    
          }
          .link_pic{
                    width: 100%;
                    height: 150px;
          }
          .link_pic img{
                    width: 100%;
                    height: 100%;
                    
          }
          .link_content{
                    padding-top: 5px;
                    font-size: 10pt;
          }
          .list_main_container{
                    
                    justify-content: center;
          }
}