.header{
          width: 100%;
          position: fixed;
          background-color: rgb(255, 255, 255);
          top: 0;
          left: 0;
          z-index: 1;
}
a{
          cursor: pointer;
}
.main{
          padding-top: 80px;
}
.cart{
          position: fixed;
          right: 10px;
          top: 80px;
          transition: all 0.2s 0.2s ease-in;
          -moz-transition: all 0.2s 0.2s ease-in;
          -webkit-transition: all 0.2s 0.2s ease-in;
          width: 40%;
          
          background-color: rgb(255, 255, 255);
          z-index: 1;
          padding-bottom: 5px;
          border: 1px solid;
          outline: 0;
          box-shadow: 0 0 3px 3px rgb(178, 178, 178);
          
}
.hide{
          transform:scaleY(0);
}
.show{
          transform: scaleY(1);
}
.cart .cart_container{
          max-height: 300px;
          padding: 5px;
          overflow-y: hidden;
}
.cart_container table{
          border-collapse: collapse;
          width: 100%;
          
}
table, th, td, tr,tbody,thead{
          border: 0.5px solid black;
          text-align: center;
          font-size: 10pt;
}
td img{
          width: 90px ;
          height: 90px;
}
@media only screen and (max-width: 1023px) {
          .cart{
                    width: 90%;
          }
}