.about-us {
    overflow-x: hidden;
}


.about_container{
    display: flex;
    width: 100%;
    padding-left: 20px;
    padding-right: 20px;

}
.abuout_pic{
    width: 100%;
    padding-left: 10px;
    display: flex;

}
.abuout_pic img{
    width: 50%;
    
}
.abuout_content{
    width: 100%;

}
.about_container1 {
    text-align: center;
}

.about_container1 h2{
    color: rgb(14, 178, 14);
    font-size: 200%;
    
    border-bottom:2px dashed;
    padding-bottom: 3px;
}
@media only screen and (max-width:1023px) {
    .about-us {
        text-align: center;
    }
    
    .about_container1{
        flex-direction: column;
    }

    .abuout_content {
        width: 100%;
    }

}

.about-us h3 {
    text-align: center;
    color: rgb(14, 178, 14);
    font-size: 200%;
    border-bottom:2px dashed;
    padding-bottom: 3px;
}