.navbar {
        width: 100%;
        display: flex;
        justify-content: space-between;
        border: 1px solid black;
        box-shadow: 0 0 5pt 2pt #747070;
        z-index: -1;

}

.navbar a:hover {
        color: #ffffff;
        text-shadow: 0 0 0.8em rgb(0, 0, 0);
}

.navbar_container {
        width: 100%;
        height: 60px;
        line-height: 60px;
        padding-left: 20px;
        padding-right: 20px;
}

a {
        color: black;
}

.navbar_mid a {
        padding: 15px 15px 15px 0;
        color: black;
        text-transform: uppercase;
        font-size: 13pt;
        font-weight: 500;
}

.navbar_logo .logo {
        font-weight: 600;
        font-size: 16pt;
        text-align: center;
        margin-left: 10px;
}

.navbar>div {
        display: flex;
}

.navbar_mid {
        display: flex;
        justify-content: space-between;
        width: 65%;
        padding-right: 70px;
}

.navbar_mid div {
        font-size: 14pt;
        background: rgb(255, 255, 255);

}

.navbar_right {
        display: flex;
}
.navbar_right div{
        padding-left: 10px;
}
.nav_link {
        width: auto;
        padding-left: 5px;
        padding-right: 5px;
}

.search input {
        height: 30px;
        width: 300px;
        padding-left: 10px;
        padding-right: 10px;
        background-color: #e6e6e6;
        border: none;
        border-bottom: 1px solid;
}
.search input:focus{
        outline: none;
        box-shadow: 0 0 1px 1px rgb(205, 205, 205);
}
@media only screen and (max-width: 1024px) {
        .button_close {
                display: none;
        }
}

@media only screen and (max-width: 1264px) {
        .icon_name {
                display: none;
        }
}
.nav_btn {
        margin-top: 10px;
        padding: 2px 12px;
        border: 1px solid black;
        display: none;
        height: 40px;
}

.no{
        display: none;
}
.button_close {
        display: none;
}
@media only screen and (max-width: 1026px) {
        .button_close {
                display: block;
        }
        .navbar_mid a{
                padding-right: 100px;
                font-size: 10pt;
        }
        .navbar_mid {
                position: fixed;
                top: 0;
                width: 200px;
                left: 0;
                transition: all 0.5s 0.2s ease-in;
                -moz-transition: all 0.5s 0.2s ease-in;
                -webkit-transition: all 0.5s 0.2s ease-in;
                flex-direction: column;
                border: 0.5px solid;
                height: 100%;
                background-color: #fff;
                padding-left: 15px;
                justify-content: flex-start;
                margin: 0;
                padding-right: 5px;
                padding-top: 10px;
        }
        .navbar_container{
                justify-content: space-between;
        }
        .navbar_logo{
                display: flex;
        
        }
        
        .nav_link:hover> a {
               color: #fff;
        }
        .nav_link:hover{
                background-color: black;
        }
        .out {
                transform: translateX(-100%);
        }

        .in {
                transform: translateX(0%);
        }
        .search input{
                width: auto;
        };
        .navbar_mid div {
                margin: 0;
                padding: 0;
                line-height: 40px;
        }

        .nav_btn {
                display: block;
        }


        .button_close {
                margin-left: 165px;
                width: 20px;
        }
        .yes{
                display: none;
        }
        .no{
                display: block;
        }
}
.notice{
        padding: 2px 5px ;
        border-radius: 50%;
        background-color: red;
        font-size: 9pt;
        color: #ffffff;
        margin-left: 0px;
        margin-top: 10px;

}